@import url('https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Inter", serif;
}
body{
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #2abfec;
}
.bmi-calculator{
  width: 800px;
  height: 500px;
  padding: 20px;
  background-color: aliceblue;
  border-radius: 9px;
  box-shadow: 0 0 10px rgba(0,0, 0, 0.1);
  display: flex;

}
.Box{
   width: 350px;
   background-image: url("/public/image/BMI.png");
   background-repeat: no-repeat;
   background-position: center;
   background-size: cover;
}
.data{
  flex: 1;
  padding: 15px;
}
h1{
  font-weight: 500;
  margin-bottom: 20px;
  padding: 10px 0px;
  text-transform: uppercase;
  font-size: 20px;
  color: #2980b9;
}
.input-container{
 margin-bottom: 15px;
}
label{
  display: block;
  margin-bottom: 5px;
 color: #888;
}
input{
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border: none;
  margin-bottom: 10px;
  border-radius: 4px;
  font-size: 15px;
}
input:hover{
  border: 1px solid #3498db;
}
button{
  background-color: #3498db;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
}
button:hover{
  background-color: #2980b9;
}
.result{
  margin-top: 20px;
  padding: 10px;
  border: 1px solid #3498db;
  border-radius: 4px;
}
.result p{
  margin: 10px 0;
  color: #555;
}
.result p:first-child{
  color: #3498db;
  font-weight: bold;
}
.error{
  color: red;
  font-size: 14px;
  margin-bottom: 10px;
}
.data button:nth-of-type(2){
  color:white;
  background-color: red;
}
.data button:nth-of-type(2):hover{
  background-color: rgb(252, 99, 99);
}